.home {
  display: flex;
  height: 68vh;
  margin-top: 7.1em;
}

.home-left {
  width: 47.54%;
  height: 100%;
  background: linear-gradient(270deg, rgba(249, 250, 254, 0.42), rgba(223, 230, 249, 0.42));
  margin-left: 7.2em;
  border-radius: 6.4%;
  display: flex;
  flex-wrap: wrap;
  cursor: pointer;
}

.home-left > p {
  width: 50%;
  height: 50%;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.home-left > p > span {
  font-family: SFNSDisplay;
  color: #333;
  font-size: 2.2em;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.home-left > p > span > i {
  font-size: 2.4em;
  color: #D6A1FC !important;
}

.home-left > p:nth-of-type(1) {
  border-right: 1px dotted #5e86f5;
  border-bottom: 1px dotted #5e86f5;
}

.home-left > p:nth-of-type(1) i {
  font-size: 2.6em;
}

.home-left > p:nth-of-type(2) {
  border-bottom: 1px dotted #5e86f5;
}

.home-left > p:nth-of-type(2) i {
  font-size: 2.7em;
}

.home-left > p:nth-of-type(3) {
  border-right: 1px dotted #5e86f5;
}

.home-left > p:nth-of-type(3) i {
  font-size: 2.4em;
}

.home-right {
  width: 33.46%;
  height: 100%;
  background-color: #F2F2F2;
  margin-left: 4.4em;
  border-radius: 7%;
  overflow: hidden;
}

.home-right > div:nth-of-type(1) {
  height: 14.3em;
  margin-top: -1em;
  background-image: url("../../../static/images/todolist-bg.png");
  background-size: 100% 100%;
  background-repeat: no-repeat;
}

.home-right > div:nth-of-type(1) > p:nth-of-type(1) {
  font-family: HelveticaNeue-Light, HelveticaNeue;
  font-weight: 300;
  color: white;
  text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.5);
  margin: 0;
  margin-left: 1.03em;
  margin-top: 0.17em;
  font-size: 6.3em;
}

.home-right > div:nth-of-type(1) > p:nth-of-type(2) {
  font-family: PingFangSC-Light, PingFang SC;
  font-weight: 300;
  color: white;
  text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.5);
  margin: 0;
  margin-left: 3.6em;
  margin-top: -.6em;
  font-size: 2em;
}
